@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.2.2/dist/css/bootstrap.min.css');
body {
  min-height: 100vh;
  background: rgb(198, 158, 217);
  background: linear-gradient(180deg, rgba(198, 158, 217, 1) 0%, rgba(148, 227, 233, 0.9812967423297444) 100%);
}

.App {
  display: flex;
  justify-content: center;
}

.container,
.feed_form {
  display: flex;
  align-items: center;
  flex-flow: column;
}

label,
input {
  font-size: 1.4rem;
}

input[type="text"] {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid black;
  border-radius: 30px;
  padding-left: 20px;

}

button {
  padding: 10px 50px;
  margin-top: 10px;
  border: 2px solid black;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1.4rem;
  background-color: rgb(138, 233, 222);
}



#formData {
  width: 80vw;
  height: auto;
  border: 2px solid black;
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  background-color: rgb(147, 209, 215);
}

.display_data {
  width: 40%;
  height: fit-content;
  padding: 15px;
  margin: 15px 20px;
  border: 2px solid black;
  border-radius: 25px;
  color: black;
  text-align: center;
  background-color: rgb(243, 140, 157);
  font-size: 1.2rem;
}